import {History} from "@reach/router"
import {PageProps} from "gatsby"
import {GatsbySeo} from "gatsby-plugin-next-seo"
import {useContext, useEffect, useState} from "react"
import TransitionContext from "~/contexts/TransitionContext"
import {useSocialNetworksQuery} from "~/queries/SocialNetworksQuery"
import {classes} from "~/utilities/classes"
import {default as NavBar} from "./NavBar/NavBar"
import NavMenu from "./NavMenu/NavMenu"
import Footer from "./Sections/Footer/Footer"

import "~/styles/style.css"
import "~/styles/typography.css"
import "./Layout.css"
import CookieSolution from "./UI/Iubenda/CookieSolution"
import TagManager from "./UI/TagManager/TagManager"

interface LayoutProps extends React.HTMLAttributes<HTMLDivElement>, Omit<PageProps, "children"> {
    history?: History | undefined
}

const Layout: React.FC<LayoutProps> = props => {
    const {
        className,
        pageResources,
        data,
        location,
        navigate,
        history,
        pageContext,
        params,
        path,
        serverData,
        uri,
        children,
        ...otherProps
    } = props
    const [menuVisible, setMenuVisible] = useState(false)
    const transitionContenxt = useContext(TransitionContext)
    const socialNetworks = useSocialNetworksQuery()

    const showMenu = (event: React.MouseEvent<HTMLElement>) => {
        setMenuVisible(true)
    }

    const hideMenu = (event: React.MouseEvent<HTMLElement>) => {
        setMenuVisible(false)
    }

    useEffect(() => {
        if (!transitionContenxt.isLoading) setMenuVisible(false)
    }, [transitionContenxt.isLoading])

    return (
        <div className={classes("layout-6jam", className)} {...otherProps}>
            <TagManager />
            <GatsbySeo
                title="Mobile Life Expert"
                titleTemplate="AdKaora | %s"
                // {...(socialNetworks?.facebookAppId ? { facebook: { appId: socialNetworks.facebookAppId } } : {})}
                openGraph={{
                    url: `${process.env.SITE_URL}`,
                    title: `${process.env.SITE_TITLE}`,
                    images: [
                        {
                            url: `${process.env.SITE_URL}/icons/icon-512x512.png`,
                            width: 512,
                            height: 512,
                            alt: "AdKaora Logo",
                        },
                    ],
                    site_name: `${process.env.SITE_TITLE}`,
                }}
                twitter={{
                    handle: `@${socialNetworks?.twitterDefaultHandleSite}`,
                    site: `@${socialNetworks?.twitterDefaultHandleSite}`,
                    cardType: "summary",
                }}
            />
            <NavMenu visible={!transitionContenxt.isLoading && menuVisible} onClose={hideMenu} />
            <NavBar onMenuIconClick={showMenu} />
            {children}
            <Footer />
            <CookieSolution />
        </div>
    )
}

export default Layout
