import ChevronUpIcon from "~/icons/ChevronUpIcon"
import {classes} from "~/utilities/classes"
import "./BackToTop.css"

interface BackToTopProps extends React.HTMLAttributes<HTMLDivElement> {}

const BackToTop: React.VFC<BackToTopProps> = props => {
    const {className, children, ...otherProps} = props

    const goBackToTop = (event: React.MouseEvent<HTMLButtonElement>) => {
        window.scrollTo({top: 0, behavior: "smooth"})
    }

    return (
        <div className={classes("back-to-top-h2fr", className)} {...otherProps}>
            <button className="btt-container-h2fr" onClick={goBackToTop}>
                <ChevronUpIcon className="icon-h2fr" />
            </button>
        </div>
    )
}

export default BackToTop
