import {forwardRef, useState} from "react"
import HiddenIcon from "~/icons/HiddenIcon"
import VisibleIcon from "~/icons/VisibleIcon"
import {classes} from "~/utilities/classes"
import "./InputText.css"

interface InputTextProps extends Omit<React.InputHTMLAttributes<HTMLInputElement>, "placeholder"> {
     containerClassName?: string | undefined
     placeholder?: string | null | undefined
     invalid?: boolean
}

const InputText = forwardRef<HTMLInputElement, InputTextProps>((props: InputTextProps, ref) => {
    const {className, type, invalid, placeholder, containerClassName, ...otherProps} = props
    const [visible, setVisible] = useState(false)

    const _type: React.InputHTMLAttributes<HTMLInputElement>["type"] =
        type === "password" && visible === false ? "password" : "text"

    return (
        <div className={classes("input-container-ume4", containerClassName)}>
            <input
                className={classes("input-text-ume4", invalid ? "invalid-ume4" : "", className)}
                ref={ref}
                type={_type}
                placeholder={placeholder ? placeholder : ""}
                {...otherProps}
            />
            {type === "password" && (
                <button className="button-ume4" type="button" onClick={() => setVisible(state => !state)}>
                    {visible ? (
                        <HiddenIcon className="password-visibility-icon-ume4" />
                    ) : (
                        <VisibleIcon className="password-visibility-icon-ume4" />
                    )}
                </button>
            )}
        </div>
    )
})

export default InputText
