export function classes(...names: Array<Classes>) {
    const list: Array<string> = []

    for (const item of names) {
        if (! item) {
            continue
        }

        if (typeof item === 'string') {
            list.push(item)
            continue
        }

        if (Array.isArray(item)) {
            list.push(classes(...item))
            continue
        }

        if (Object.getPrototypeOf(item).constructor === Object) {
            for (const name in item) {
                if (item[name as keyof typeof item]) {
                    list.push(name)
                }
            }
            continue
        }
    }

    return list.join(' ')
}

// Types ///////////////////////////////////////////////////////////////////////

export type Classes =
    | undefined
    | null
    | string
    | Record<string, boolean | null | undefined>
    | Array<Classes>
    | readonly Classes[]
    // | readonly [...Array<Classes>]
